.background {
  background-image: url("/build/images/pattern-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 2rem;
}
.background .title {
  color: #ffff;
}

.input {
  display: flex;
  justify-content: center;
  align-items: center;
}

input {
  padding: 1rem;
  border-radius: 1rem 0 0 1rem;
  border: 1px solid #ffff;
}
@media (min-width: 768px) {
  input {
    width: 40%;
    padding: 1.7rem;
  }
}
input:focus {
  outline: 1px solid #453e99;
  border: 1px solid #453e99;
}

button {
  background-color: #2b2b2b;
  border-radius: 0 1rem 1rem 0;
  padding: 1.11rem;
  border: #969696;
}
button:hover {
  background-color: #383838;
}
@media (min-width: 768px) {
  button {
    padding: 1.797rem;
  }
}